let tree: ExplorerType = {};

const traverse = (parent: ExplorerType, path: Array<string>, data: ExplorerType): object => {
    const entry = path[0];

    if (!parent.hasOwnProperty(entry)) {
        parent = { ...parent, [entry]: data.mode === "100644" ? data : {} };
    }

    if (path.length > 1) {
        const node = traverse(parent[entry], path.slice(1), data);
        parent = { ...parent, [entry]: node };
    }

    return parent;
}

const fetchGitHubReops = async (): Promise<ExplorerType> => {
    // const { slug } = await appOctokit.request("GET /user");
    // console.log("authenticated as %s", slug);

    const repos = await appOctokit.request("GET /repos/{owner}/{repo}/commits?sha=main", {
        owner: "imdev912",
        repo: "imdev912.github.io"
    });

    if (repos.status === 200) {
        const data = repos.data;
        const commit = data[0];
        const sha: string = commit.sha;

        const files = await appOctokit.request("GET /repos/{owner}/{repo}/git/trees/{tree_sha}?recursive=true", {
            owner: "imdev912",
            repo: "imdev912.github.io",
            tree_sha: sha
        });

        if (files.status === 200) {
            const repoData = files.data.tree;

            repoData.forEach((item: ExplorerType) => {
                const path = item.path ? item.path.split('/') : [];
                tree = traverse(tree, path, item);
            });

            return tree;
        }
    }

    return {};
}


const sortObj = (obj: ExplorerType): ExplorerType => {
    return Object.keys(obj).sort().reduce((result: ExplorerType, key) => {
        let data = obj[key];

        if (typeof data === "object") {
            data = sortObj(data);
        }

        result[key] = data;
        return result;
    }, {});
}

const Content = () => {
    const [loading, setLoading] = useState(true);
    const [progress, setProgress] = useState<number>(0);
    const [explorer, setExplorer] = useState<ExplorerType>({});

    useEffect(() => {
        (async () => {
            const repoData = await fetchGitHubReops();
            const repos = sortObj(repoData);
            setExplorer(repos);
            setLoading(false);
            setProgress(100);
        })();
    }, []);

    return (
        <>
            {
                loading ? (
                    <Loader value={progress} />
                ) : (
                    <div className="container">
                        <Explorer explorer={explorer} expand={true} />
                    </div>
                )
            }
        </>
    )
}

export default Content;