import {
    CircularProgressbarWithChildren,
    buildStyles
} from "react-circular-progressbar";
import { CircularProgressbarWrapperProps } from "react-circular-progressbar/dist/types";
import "react-circular-progressbar/dist/styles.css";
import { Fade } from "react-awesome-reveal";

interface Separators {
    turns: number;
    style: React.CSSProperties;
}

const Separator: React.FC<Separators> = ({
    turns,
    style
}) => {
    return (
        <div
            style={{
                position: "absolute",
                height: "100%",
                transform: `rotate(${turns}turn)`
            }}
        >
            <div style={style} />
        </div>
    );
}

interface RadialSeparatorsProps {
    count: number;
    style: React.CSSProperties;
}

const RadialSeparators: React.FC<RadialSeparatorsProps> = ({
    count,
    style,
}): React.ReactElement<any, any> => {
    const turns = 1 / count;
    const separators = Array.from({ length: count }, (_, i) => (
        <Separator turns={i * turns} style={style} />
    ));

    return (
        <div style={{
                marginBottom: "80px",
                marginRight: "5px"
            }}
        >
            {separators}
        </div>
    );
};

const Loader: React.FC<CircularProgressbarWrapperProps> = ({ value }) => {
    const strokewidth = 10;
    return (
        <div className="loader-container">
            <div className="loader">
                <Fade delay={1000}>
                    <CircularProgressbarWithChildren
                        value={value}
                        minValue={0}
                        maxValue={100}
                        text={`${value}%`}
                        strokeWidth={strokewidth}
                        styles={buildStyles({
                            pathColor: "#ff3f81",
                            textColor: "#ffffff",
                            strokeLinecap: "butt"
                        })}
                    >
                        <RadialSeparators
                            count={12}
                            style={{
                                background: "#ff3f81",
                                width: "5px",
                                height: `${strokewidth}%`
                            }}
                        />
                    </CircularProgressbarWithChildren>
                </Fade>
            </div>
        </div>
    );
};

export default Loader;