import { useState, useEffect, useRef } from 'react';
import VANTA_GLOBE, { VantaGlobeInstance } from 'vanta/dist/vanta.globe.min';

const Background = () => {
    const [vantaEffect, setVantaEffect] = useState<VantaGlobeInstance | null>(null)
    const myRef = useRef<HTMLDivElement>(null);

    useEffect(() => {
        if (!vantaEffect && myRef.current) {
            setVantaEffect(VANTA_GLOBE(myRef.current, {}));
        }

        return () => {
            if (vantaEffect) vantaEffect.destroy()
        }
    }, [vantaEffect]);

    return (
        <div className="background" ref={myRef}>
        </div>
    )
}

export default Background;